
import Toast from 'vux/src/components/toast/index.vue'
import XHeader from 'vux/src/components/x-header/index.vue'
import Cell from 'vux/src/components/cell/index.vue'
import XButton from 'vux/src/components/x-button/index.vue'
import PopupRadio from 'vux/src/components/popup-radio/index.vue'
import Group from 'vux/src/components/group/index.vue'
import Flexbox from 'vux/src/components/flexbox/flexbox.vue'
import FlexboxItem from 'vux/src/components/flexbox/flexbox-item.vue'
import TransferDom from 'vux/src/directives/transfer-dom/index.js'
import Confirm from 'vux/src/components/confirm/index.vue'
import Loading from 'vux/src/components/loading/index.vue'

import axios from "axios";
import url from "../../config/url.js";

export default {
directives: {
TransferDom
},
components: {
Toast,
XHeader,
Cell,
XButton,
PopupRadio,
Group,
Confirm,
Loading,
Flexbox,
FlexboxItem
},
data() {
return {
data: [],
option3: "",
options1: ["手机设置"],
option1: "",
options3: ["定时", "定电量", "定金额", "自动充满"], // 充电类型

option4: "",
options4: [],
showStartBtn: false,
showTimer: false,
show: false,
typeTitle: "",
radioMsg: "",
type: "",
setType: "",
showLoading: false,
showSuccess: false,
showWaring: false,
setPhone: false,
dataMsg: "",
pid: "",
token: ""
};
},
created() {
let _chargeId = this.$route.query.chargeId;







this.token = localStorage.getItem("token");
this.option1 = "手机设置";
this.option3 = "自动充满";

if (_chargeId.length> 0) {
localStorage.removeItem("platform");
this.getQrCode();

}





},
watch: {
option1(val) {
console.log(val);
if (val == "手机设置") {
this.showStartBtn = true;
this.setPhone = true;
this.setType = 0;
} else {
this.setType = 1;
this.setPhone = false;
this.showTimer = false;
this.type = "";
this.option4 = "";
this.showStartBtn = false;
}
},
option3(val) {

if (!val == "") {
this.showTimer = true;
}
if (val == "定时") {
this.type = 1;
this.typeTitle = "定时";
this.radioMsg = "请选则充电时间(单位小时)";
this.option4 = "";
this.options4 = ["1", "2", "3", "4"];
this.showStartBtn = true;
}
if (val == "定电量") {
this.type = 2;
this.typeTitle = "定电量";
this.radioMsg = "请选则充电电量(单位千瓦时)";
this.option4 = "";
this.options4 = [
"5",
"10",
"15",
"20",
"25",
"30",
"35",
"40",
"45",
"50"
];
this.showStartBtn = true;
}
if (val == "定金额") {
this.type = 3;
this.typeTitle = "定金额";
this.radioMsg = "请选则充电金额(单位元)";
this.option4 = "";
this.options4 = ["5", "10", "20", "30", "40", "50", "80", "100"];
this.showStartBtn = true;
}
if (val == "自动充满") {
this.type = 4;
this.option4 = 0;
this.showTimer = false;
this.showStartBtn = false;
}
},
option4(val) {

if (!val == "") {
this.showStartBtn = false;
}
}
},
methods: {
back() {

this.$router.push({
path: "/home/bmapV"
});
},
jump(path) {
this.$router.push(path);
},
startCharge() {

this.show = true;
},
platfromStartCharging(Ctype,value) {
let vm = this;
var token = localStorage.getItem("token");
let _url = url.PLATFORM + "/evcs/20160701/start_charging";
axios({
method: "post",
url: _url,
headers: { token: token },
data: {
model: Ctype,
value: value,
QRCode: vm.$route.query.chargeId
}
}).then(res => {
if (res.data.code == 200) {
vm.showLoading = false;
vm.showSuccess = true;
console.log("新建的订单："+res.data.data);
localStorage.setItem("platformOID", res.data.data);
setTimeout(function() {
vm.jump("/home/user/order");
}, 1000);
}
});
},
confirm() {

let vm = this;
vm.show = false;
vm.showLoading = true;
let value = "";
let Ctype = vm.type;
if (vm.type == 1) {
value = vm.option4 * 3600;
}
if (vm.type == 2) {
value = vm.option4;
}
if (vm.type == 3) {
value = vm.option4 * 100;
}
if (vm.type == 4) {
Ctype = 1;
value = 36000;
}
var flag = localStorage.getItem("platform");


if (flag == "tled") {
vm.platfromStartCharging(Ctype,value)
return;
}

axios({
method: "post",
url: url.LOCALSRC + "/app/charging",
headers: { token: vm.token },
data: {
type: vm.setType,
model: Ctype,
value: value,
pid: vm.pid
}
}).then(res => {
if (res.data.code == 200) {
vm.showLoading = false;
vm.showSuccess = true;
setTimeout(function() {
vm.jump("/home/user/order");
}, 1000); vm.showLoading = false;
vm.showSuccess = true;
setTimeout(function() {
vm.jump("/home/user/order");
}, 1000);
} else {
vm.showLoading = false;
vm.showWaring = true;
vm.dataMsg = res.data.message;
setTimeout(function() {
vm.jump("/home/bmapV");
}, 1000);
}
});
},
platformQrcode() {
let vm = this;
let _chargeId = vm.$route.query.chargeId





let _url = url.PLATFORM + "/evcs/20160701/charging/qrCode?QRCode=" + _chargeId;
axios({
method: "get",
url: _url,
headers: { token: vm.token }
}).then(res => {
console.log("======================");
console.log(res);
if (res.data.code == 200) {

} else {
vm.$vux.confirm.show({
title: "提示",
content: "此充电桩正在使用或暂时无法使用",
confirmText: "确认",
showCancelButton: false
});

vm.jump("/home/bmapV");
}
});
},
getQrCode() {

let vm = this;
console.log(vm.token);
this.$ajax({
method: "get",
url: "app/charging/qrCode?qrCode=" + this.$route.query.chargeId,
headers: { token: vm.token }
}).then(res => {
console.log('ddd',res);
if (res.data.code == 200) {
vm.pid = res.data.data.pid;
if (vm.pid == undefined) {

vm.$vux.confirm.show({
title: "提示",
content: "此充电桩正在使用或暂时无法使用",
confirmText: "确认",
showCancelButton: false
});

vm.jump("/home/bmapV");
}
console.log(vm.pid + "测试是否有值");
} else {
vm.showWaring = true;
vm.dataMsg = res.data.message;

setTimeout(function() {
vm.jump("/home/bmapV");
}, 1000);
}
});
},

takeLine() {
let vm = this;
let pid = Number(vm.$route.query.chargeId);
vm.$ajax({
method: "post",
url: "app/charging/motor",
headers: { token: vm.token },
data: {
pid: pid,
model: 5
}
}).then(res => {
console.log(res);
});
},

palyLine() {
let vm = this;
let pid = Number(vm.$route.query.chargeId);
vm.$ajax({
method: "post",
url: "app/charging/motor",
headers: { token: vm.token },
data: {
pid: pid,
model: 6
}
}).then(res => {
console.log(res);
});
}
}
};



