
import XButton from 'vux/src/components/x-button/index.vue'
import Box from 'vux/src/components/box/index.vue'
import Radio from 'vux/src/components/radio/index.vue'
import Group from 'vux/src/components/group/index.vue'
import Toast from 'vux/src/components/toast/index.vue'
import XInput from 'vux/src/components/x-input/index.vue'

import URL from '../../../config/url.js'
import {format} from '../../../config/format.js'
import store from '../../../vuex/store.js'
import axios from 'axios'

export default {
components: {
XButton,
Box,
Group,
Radio,
Toast,
XInput
},
methods: {
linkToAccount(){
this.$router.push('/home/mine')
},
createCharge(){
let qs = require('qs');
let pingpp = require('pingpp-js');
let router = this.$router;





axios.post(URL.createCharge, qs.stringify({channel : 'wx_pub', amount : this.chargeSum, openid : this.$store.getters.getOpenid, phone : this.$store.getters.getPhone})).then(function(response) {
console.log(response.data)
let charge = response.data;
pingpp.createPayment(charge, function(result, err){
console.log(result)
if (result == "success") {

router.push('/home/user/account');
} else if (result == "fail") {

router.push('/home/user/account/charge/error');
} else if (result == "cancel") {


}
});
}).catch(function(error) {
console.log(error);
})

}
},
data () {
return {
balance:'XXX',

chargeWay:['微信'],
show:false,
chargeWayValue:'微信',
openid: store.getters.getOpenid,
chargeSum:'',
};
},
}



