
import XHeader from 'vux/src/components/x-header/index.vue'
import Cell from 'vux/src/components/cell/index.vue'
import XButton from 'vux/src/components/x-button/index.vue'
import Panel from 'vux/src/components/panel/index.vue'
import Grid from 'vux/src/components/grid/grid.vue'
import GridItem from 'vux/src/components/grid/grid-item.vue'
import Actionsheet from 'vux/src/components/actionsheet/index.vue'
import Checker from 'vux/src/components/checker/checker.vue'
import CheckerItem from 'vux/src/components/checker/checker-item.vue'
import XTextarea from 'vux/src/components/x-textarea/index.vue'
import Group from 'vux/src/components/group/index.vue'

export default {
components:{
XHeader,
Cell,
XButton,
Panel,
Grid,
GridItem,
Actionsheet,
Checker,
CheckerItem,
XTextarea,
Group
},
data () {
return {
failMsg:'',
demo1: '',
menus2: null,
menus1 :{
menu1: '提交'
},
showProp: false,
showFail: false,
data: new Array(),
list: new Array(),
}
},
created () {


this.GetMsg()
},
methods:{

Back () {

this.$router.push({
path:'/chargeDetails/tabDetails',
query:{
id:this.$route.query.id
}
})
},

ShareChargeList () {

let vm = this
if (sessionStorage.getItem('token')==null) {
vm.$router.push('/login')
} else {
vm.showProp = true
}
},

GetMsg () {

let vm = this
vm.$ajax({
method:"get",
url:'app/siteAuxiliary/site/'+vm.$route.query.id+'/chargingRecord',
headers:{'token':vm.token}
})
.then(res=>{
if(res.data.code==200){
vm.data = res.data.data.list
vm.data.map((x)=>{
vm.list.push(
{
src: 'http://img4.duitang.com/uploads/item/201408/12/20140812102945_anHrL.jpeg',
title: x.status == 0?'成功':'失败',
desc: x.content,
meta: {
source: x.name,
date: x.ctime,
other: '其他信息'
}
}
)
vm.list.reverse()
})
}
})
},

PileFail () {

let vm = this
vm.showProp = false
vm.showFail = true
},

Post (id,status,content) {
let vm = this
vm.$ajax({
method: 'post',
url: '/app/siteAuxiliary/site/chargingRecord',
headers:{'token':sessionStorage.getItem('token')},
data: {
sid: id,
status: status,
content: content
}
})
.then(res=>{
if (res.data.code ==200){
vm.showProp = false
vm.showFail = false
vm.GetMsg()
}
}).catch(err=>{console.log(err)})
},

pileok () {

let vm = this
vm.Post(vm.$route.query.id,0,null)
},

SubmitList () {

console.log(132)
let vm = this
vm.Post(vm.$route.query.id,1,vm.failMsg)
}
}
}



