
import store from '../../../config/store.js'
import Car from '../../../config/car.js'
import XHeader from 'vux/src/components/x-header/index.vue'
import Group from 'vux/src/components/group/index.vue'
import Cell from 'vux/src/components/cell/index.vue'
import XButton from 'vux/src/components/x-button/index.vue'
import Flexbox from 'vux/src/components/flexbox/flexbox.vue'
import FlexboxItem from 'vux/src/components/flexbox/flexbox-item.vue'
import PopupPicker from 'vux/src/components/popup-picker/index.vue'
import Alert from 'vux/src/components/alert/index.vue'
import TransferDom from 'vux/src/directives/transfer-dom/index.js'

export default {
directives: {
TransferDom
},
components: {
XHeader,
Group,
Cell,
XButton,
Flexbox,
FlexboxItem,
PopupPicker,
Alert
},
data () {
return {
show:false,
title: '车辆品牌',
list: [],
list1: [],
value: [],
value1: [],
}
},
methods:{
jump (url) {
this.$router.push(url)
},
onShow () {
console.log('on show')
},
onChange (val) {
this.value1 = []
this.list1 = Car.CarType(val[0])
},
onHide (type) {
console.log('on hide', type)
},
Choose(){
if(this.value.length == 0 || this.value1.length == 0){
this.show = true
}else{
let vm = this
let name = this.$route.query.name
let vehicleType = this.value[0] + "--" + this.value1[0]
vm.$ajax({
method:'put',
url:'/app/personalCenter',
headers:{'token':sessionStorage.getItem('token')},
data:{
name: name,
vehicleType: vehicleType,
}
})
.then(res=>{
if(res.data.code==200){
setTimeout(
function(){
vm.jump('/home/bmapV')
},1000
)
}
})
}
},
pass(){
this.jump('/home/bmapV')
}
},
created () {
this.list = Car.CarName
}
}



