
import Group from 'vux/src/components/group/index.vue'
import Cell from 'vux/src/components/cell/index.vue'
import XInput from 'vux/src/components/x-input/index.vue'
import XButton from 'vux/src/components/x-button/index.vue'
import Box from 'vux/src/components/box/index.vue'
import Toast from 'vux/src/components/toast/index.vue'
import XHeader from 'vux/src/components/x-header/index.vue'

import URL from "../../config/url.js";
import axios from "axios";

export default {
components: {
Group,
Cell,
XInput,
XButton,
Box,
Toast,
XHeader
},
created: function() {},
data() {
return {
phone: "",
smsCode: "",
inputSmsCode: "",
show: false,
toastShow: {
showCancel: false,
cancel_msg: "",
showSuccess: false,
success_msg: ""
},
inputphone: "",
disable001: false,
btn: "发送验证码",
passWord: ""
};
},
methods: {
jump(path) {
this.$router.push(path);
},
sendSmscode(codeid, message) {
let vm = this;

var qs = require("qs");
vm.inputphone = vm.phone;
axios
.post(
URL.sendSmsCode,
qs.stringify({
codeid: codeid,
action: "code",
username: 302605782,
userpass: 302605782,
mobiles: vm.phone,
content: message
}),
{ headers: { "Content-Type": "application/x-www-form-urlencoded" } }
)
.then(function(response) {
console.log(response.data);
vm.toastShow.success_msg = "验证码发送成功";
vm.toastShow.showSuccess = true;
})
.catch(function(error) {
console.log(error);
});
},
getSmscode() {
var Num = "";
for (var i = 0; i < 6; i++) {
Num += Math.floor(Math.random() * 10);
}
return Num;
},
sendVCode() {
let vm = this;
var countdown = 60;
this.smsCode = this.getSmscode();

if (this.$refs.input01.valid) {
vm.$ajax({
method: "get",
url: "/app/personalCenter/validate",
params: {
phone: vm.phone
}
}).then(result => {
if (result.data.code != 200) {
vm.toastShow.cancel_msg = "手机号未注册";
vm.toastShow.showCancel = true;
} else {
vm.disable001 = true;
console.log(vm.smsCode);
vm.sendSmscode(4774, vm.smsCode);
vm.disable001 = true;
var interval = setInterval(function() {
if (countdown > 0) {
vm.btn = "重新发送(" + countdown + ")";
countdown--;
} else {
vm.disable001 = false;
vm.btn = "发送验证码";
clearInterval(interval);
}
}, 1000);
}
});
} else {
vm.toastShow.cancel_msg = "请输入正确的手机号码！";
vm.toastShow.showCancel = true;
}
},
modifyPassword() {
let vm = this;

if (vm.inputphone != vm.phone) {
vm.toastShow.cancel_msg = "手机号码已更换 请重新发送验证码";
vm.toastShow.showCancel = true;
} else if (vm.smsCode == "") {
vm.toastShow.cancel_msg = "请输入验证码";
vm.toastShow.showCancel = true;
} else if (vm.inputSmsCode == vm.smsCode) {
console.log("xiugaimima");


vm.$ajax({
method: "put",
url: "/app/personalCenter/password/update",
data: {
phone: vm.phone,

password: vm.passWord
}
})
.then(res => {
console.log(res);
if (res.data.code == 200) {
vm.toastShow.success_msg = "密码修改成功";
vm.toastShow.showSuccess = true;
setTimeout(function() {
vm.jump("/login");
}, 1000);
} else {
vm.toastShow.showCancel = true;
vm.toastShow.cancel_msg = "修改失败";
}
})
.catch(err => {
console.log(err);
});
} else {
vm.toastShow.cancel_msg = "验证码输入错误";
vm.toastShow.showCancel = true;
}
}
}
};



