
import $ from 'jquery';
import XHeader from 'vux/src/components/x-header/index.vue'
import Flexbox from 'vux/src/components/flexbox/flexbox.vue'
import FlexboxItem from 'vux/src/components/flexbox/flexbox-item.vue'
import XInput from 'vux/src/components/x-input/index.vue'
import XButton from 'vux/src/components/x-button/index.vue'
import Toast from 'vux/src/components/toast/index.vue'

export default {
components: {
XHeader,
Flexbox,
FlexboxItem,
XInput,
XButton,
Toast
},
data () {
return {
name:'',

invoice_djhm:'',
invoice_email:'',
show:false,
message:''
}
},
created () {
let vm = this
vm.name = sessionStorage.getItem('name')
if (sessionStorage.getItem('token') == null){
vm.jump('/login')
}
vm.invoice_djhm = vm.$route.query.djhm
},
methods:{
jump (url) {
this.$router.push(url)
},

ver_change(){
var pattern = /^([A-Za-z0-9_\-\.])+\@([A-Za-z0-9_\-\.])+\.([A-Za-z]{2,4})$/;
if(this.invoice_email == ''){
$("input[name='invoice_email']").attr('placeholder','*请填写邮箱地址').addClass('Verification_style')
}else if(!pattern.test(this.invoice_email)){
this.invoice_email = ''
$("input[name='invoice_email']").attr('placeholder','*邮箱格式错误').addClass('Verification_style')
}
},

submit_ver(){

let vm = this
if(vm.invoice_email != ''){
vm.$ajax({
method:'post',
url:'/app/invoice/sendMail',
headers:{'token':sessionStorage.getItem('token')},
data:{
djhm:vm.invoice_djhm,
email:vm.invoice_email
}
}).then(res => {
console.log("sss",res)
if(res.data.code == 200){
console.log("提交成功")
vm.$router.push({path:'/home/invoice/invoiceSuccess'})
}else{
this.show=true;
this.message=res.data.message;
}
})
}else{
vm.ver_change()
return false;
}
}
}
}



