
import TransferDom from 'vux/src/directives/transfer-dom/index.js'
import Actionsheet from 'vux/src/components/actionsheet/index.vue'
import Group from 'vux/src/components/group/index.vue'
import CellBox from 'vux/src/components/cell-box/index.vue'
import XSwitch from 'vux/src/components/x-switch/index.vue'
import Toast from 'vux/src/components/toast/index.vue'
import XHeader from 'vux/src/components/x-header/index.vue'
import CheckIcon from 'vux/src/components/check-icon/index.vue'
import XButton from 'vux/src/components/x-button/index.vue'
import Checker from 'vux/src/components/checker/checker.vue'
import CheckerItem from 'vux/src/components/checker/checker-item.vue'
import Flexbox from 'vux/src/components/flexbox/flexbox.vue'
import FlexboxItem from 'vux/src/components/flexbox/flexbox-item.vue'
import Cell from 'vux/src/components/cell/index.vue'
import XInput from 'vux/src/components/x-input/index.vue'

import axios from "axios";
import $ from "jquery";

export default {

components: {
Actionsheet,
Group,
XSwitch,
Toast,
XHeader,
CheckIcon,
XButton,
Checker,
CheckerItem,
CellBox,
Flexbox,
FlexboxItem,
Cell,
XInput
},
directives: {
TransferDom
},
data() {
return {
money: 300,
commonList: ["支付宝", "微信", "银联"],
checkType: ["银联"],
menus1: {
menu1: "微信支付",
menu2: "支付宝支付",
menu3: "银联支付"
},
loading: false,
html: "",
url: {
required: true
},
one: false,
two: false,
three: false,
height: "",
showSwitch: false,
msgSwitch: "",
Red_envelopes: "", //红包个数
redValue: 5, //充值可用红包个数
rid: "", //红包id
r_money: 0, //红包金额
moneyvalue:'',
beint:function(value){
return {
valid:Number.isInteger(Number(value)),
msg:'必须是整数'
}
},
docmHeight: document.documentElement.clientHeight,  //默认屏幕高度
showHeight: '0',   //实时屏幕高度
hidshow:true, //显示或者隐藏footer
};
},
mounted (){

window.onresize = ()=>{
return(()=>{
this.showHeight = document.body.clientHeight;
})()
}
},
watch: {

one(val) {
let vm = this;
if (val) (vm.two = false), (vm.three = false);
},
two(val) {
let vm = this;
if (val) (vm.one = false), (vm.three = false);
},
three(val) {
let vm = this;
if (val) (vm.one = false), (vm.two = false);
},
showHeight:function() {
if(this.docmHeight > this.showHeight){
this.hidshow=false
}else{
this.hidshow=true
}
}
},
created() {
let vm = this;

vm.height = document.documentElement.clientHeight;
if (sessionStorage.getItem("token") == null) {
vm.jump("/login");
}
if (localStorage.getItem("rechargeProtocol") == null) {
vm.$router.push("/home/user/rechargeProtocol")
}
if (sessionStorage.getItem("rid") != null) {
vm.rid = sessionStorage.getItem("rid");
console.log(vm.rid);
}
if (vm.$route.query.r_money != undefined) {
vm.r_money = vm.$route.query.r_money;
}
if (vm.$route.query.c_money != undefined) {
vm.money = Number(vm.$route.query.c_money);
}
if (vm.$route.query.useNum != undefined) {
vm.redValue = vm.$route.query.useNum;
}

vm.getRedEnvelopes(this.redValue);
},
methods: {
rechargeProtocol(){
console.log("充值协议")
this.$router.push("/home/user/rechargeProtocol")
},
pay() {
let vm = this;
if (vm.Verification()) {
if (vm.three) vm.unionPayPayment();
if (vm.one) vm.weChatPayment();
if (vm.two) vm.alipayPayment();
}
},
change (val) {

if(val==''||!Number.isInteger(Number(val))||val.length==1){
this.money=0
}else{
this.money=val
}

},
unionPayPayment() {

let vm = this;

var options = "location=yes,hidden=yes";

var url =
"http://106.75.154.242:9090/#/?token=" +
sessionStorage.getItem("token") +
"&moneyRmb=" +
vm.money +
"&type=1" +
"&rid=" +
vm.rid;









window.location.href = url;
},
weChatPayment() {

let vm = this;

var url = "/app/recharge/unifiedOrder";
vm.$ajax({
method: "post",
url: url,
headers: { token: sessionStorage.getItem("token") },
data: {
amount: vm.money * 100,
mode: 2,
rid: vm.rid
}
}).then(res => {
console.log(res.data.data);
var obj = JSON.parse(res.data.data);
console.log(obj);
if (res.data.code == 200) {
var params = {
partnerid: obj.partnerid, // merchant id
prepayid: obj.prepayid, // prepay id
noncestr: obj.noncestr, // nonce
timestamp: obj.timestamp, // timestamp
sign: obj.sign 
};
console.log(params);
Wechat.sendPaymentRequest(
params,
function() {
this.$router.push({
path: "/home/mineV"
});
},
function(reason) {
vm.$vux.confirm.show({
title: "提示",
content: reason,
confirmText: "确认",
showCancelButton: false
});
}
);
}
});
},
alipayPayment() {

let vm = this;

var url = "/app/recharge/unifiedOrder";
vm.$ajax({
method: "post",
url: url,
headers: { token: sessionStorage.getItem("token") },
data: {
amount: vm.money * 100,
mode: 3,
rid: vm.rid
}
}).then(res => {
console.log(res);
if (res.data.code == 200) {
console.log(res.data.data);
var url = res.data.data;

window.location.href = url;
}
});
},


















close() {
this.$router.push("/home/mineV");
},
Verification() {

let vm = this;
console.log(vm.one + vm.two + vm.three);
if (vm.money == "") {
vm.showSwitch = true;
vm.msgSwitch = "请选择充值金额";
} else if (vm.one == false && vm.two == false && vm.three == false) {
vm.showSwitch = true;
vm.msgSwitch = "请选择充值方式";
} else return true;
},

getValue(index) {

this.redValue = index;
this.getRedEnvelopes(this.redValue);
},
getRedEnvelopes(num) {
let vm = this;
vm.$ajax({
method: "get",
url: "/app/activity/unusedRedPacket",
headers: { token: sessionStorage.getItem("token") }
}).then(res => {
if (res.data.code == 200) {

let oneArr = [];
let twoArr = [];
res.data.data.map(item => {
if (item.aid == 2) {
twoArr.push(item);
} else {
oneArr.push(item);
}
});
let sliceArr = twoArr.slice(0, num + 1);
vm.Red_envelopes = oneArr.concat(sliceArr).length;
}
});
},

changeMoney() {
this.r_money = 0;
sessionStorage.setItem("rid", "");
this.rid = "";
}
}
};



