
import Cell from 'vux/src/components/cell/index.vue'
import Group from 'vux/src/components/group/index.vue'
import Box from 'vux/src/components/box/index.vue'
import XButton from 'vux/src/components/x-button/index.vue'
import Flexbox from 'vux/src/components/flexbox/flexbox.vue'
import FlexboxItem from 'vux/src/components/flexbox/flexbox-item.vue'
import XHeader from 'vux/src/components/x-header/index.vue'
import Grid from 'vux/src/components/grid/grid.vue'
import GridItem from 'vux/src/components/grid/grid-item.vue'
import Toast from 'vux/src/components/toast/index.vue'

import Tabbar from "../charge/components/tabbar"
import URL from '../../config/url.js'
import store from '../../vuex/store.js'
import {format} from '../../config/format.js'
import axios from 'axios'

export default {
components: {
XButton,Box,Group,Cell,Flexbox,FlexboxItem,XHeader,Grid,GridItem,Tabbar,Toast
},
data () {
return {
name:'未填写',
sex:'未填写',
email:'未填写',
carType:'未填写',
phone:store.getters.getPhone,
balance:'XXX',
r_num:'XXX',
showUserMsg:false,
showtoast:false,
backClick:0,
time: new Date(),
}
},
created () {
let vm=this;
console.log(new Date())
if(sessionStorage.getItem('token')==null){
this.showUserMsg = false
}else{
this.showUserMsg = true
let vm = this
vm.$ajax({
method:'get',
url:'/app/personalCenter',
headers:{'token':sessionStorage.getItem('token')}
})
.then(res=>{
console.log(res)
if(res.data.code==200){
vm.balance = (res.data.data.balance+res.data.data.hdBalance)/100;
vm.name = res.data.data.name;
vm.car = res.data.data.vehicleType;
store.UserMsg = res.data.data
console.log(store.UserMsg)
}
})
}
this.getEnvelopes()

document.addEventListener("backbutton", this.EBackButton, false);

},
beforeDestroy: function() {
document.removeEventListener("backbutton", this.EBackButton, false);
},
mounted () {
console.log(new Date())
},
methods:{




back(){
this.$router.go(-1)
},
GotoInfo (url) {
console.log('进来了')
if(sessionStorage.getItem('token')==null){
this.jump('/login')
}else{
this.jump('/home/user/info/modify')
}
},
GotoLogin () {
this.jump('/login')
},
GotoRegister () {
this.jump('/register')
},

getEnvelopes(){
let vm = this
vm.$ajax({
method:'get',
url:'/app/activity/unusedRedPacket',
headers:{'token':sessionStorage.getItem('token')},
}).then(res => {
if(res.data.code == 200){
vm.r_num = res.data.data.length
}
})
},
EBackButton() {
if (this.backClick > 0 && Date.parse(new Date()) - this.time < 2000) {

this.backClick = 0;
navigator.app.exitApp(); 
} else {
this.showtoast = true; 
if (Date.parse(new Date()) - this.time < 2000) {

this.backClick++;
} else {

this.time = Date.parse(new Date());
this.backClick = 0;
}
}

},
}
}



