
import XHeader from 'vux/src/components/x-header/index.vue'
import Group from 'vux/src/components/group/index.vue'
import Cell from 'vux/src/components/cell/index.vue'
import Scroller from 'vux/src/components/scroller/index.vue'

export default {
components: {
XHeader,
Group,
Cell,
Scroller
},
data () {
return {
name:'',
height:'auto',
str_money:'￥',
isTrue:false,
data: [],
list: [],
height:'160px'
}
},
mounted () {
let Height = window.innerHeight - 54
this.height =Height+'px'
},
created () {
let vm = this
vm.name = sessionStorage.getItem('name')
if (sessionStorage.getItem('token') == null){
vm.jump('/login')
} else {
this.Get_order_list()
}
},
methods:{
status(status,index){
if(status==1){

this.jumpDetail(index)
}
},
jump (url) {
this.$router.push(url)
},
Get_order_list () {
let vm = this
vm.$ajax({
method:'get',
url:'/app/invoice',
headers:{'token':sessionStorage.getItem('token')},
})
.then(res=>{

if(res.data.code == '200'){
vm.data = res.data.data.list
console.log("ttt",vm.data)
if(res.data.data.list.length == 0){
this.isTrue = true
}
}
})
},

jumpDetail(index){
this.$router.push({path:'/home/invoice/invoiceDetail',query:{'invoiceId':index}})
}
}
}



