

import Tabbar from 'vux/src/components/tabbar/tabbar.vue'
import TabbarItem from 'vux/src/components/tabbar/tabbar-item.vue'
import Popup from 'vux/src/components/popup/index.vue'
import Group from 'vux/src/components/group/index.vue'
import XSwitch from 'vux/src/components/x-switch/index.vue'

import store from '../vuex/store.js'
import router from '../router'
import axios from 'axios'
import wx from 'weixin-js-sdk'
import URL from '../config/url.js'
import {format} from '../config/format.js'

export default {
components: {
Tabbar, TabbarItem, Popup, Group, XSwitch
},
data () {
return {
height:'',
transitionName: 'slide-left',
show_home:true,
show_charge:true,
show_user:true
}
},
watch:{
$route (to,form) {
console.log(location.hash)

}
},
updated () {
let vm = this
switch (location.hash) {
case '#/home/chargeV':
vm.show_charge = false
vm.show_home = true
vm.show_user = true
return

case '#/home/bmapV':
vm.show_charge = true
vm.show_home = false
vm.show_user = true
return

case '#/home/mineV':
vm.show_charge = true
vm.show_home = true
vm.show_user = false
return
}
},
beforeRouteUpdate (to, from, next) {
let isBack = this.$router.isBack
if (isBack) {
this.transitionName = 'slide-right'
} else {
this.transitionName = 'slide-left'
}
this.$router.isBack = false
next()
},
methods: {
ResetScroller () {
this.$refs.scroller.reset();
}
},
created () {
let vm = this
this.height = window.screen.height-53
console.log(this.height)
var token = localStorage.getItem('token')

if(token != null){
console.log("set token")
sessionStorage.setItem('token',token)
}

}
}



