


import axios from 'axios'
import XButton from 'vux/src/components/x-button/index.vue'
import Box from 'vux/src/components/box/index.vue'
import GroupTitle from 'vux/src/components/group-title/index.vue'
import Flexbox from 'vux/src/components/flexbox/flexbox.vue'
import FlexboxItem from 'vux/src/components/flexbox/flexbox-item.vue'
import Divider from 'vux/src/components/divider/index.vue'
import Cell from 'vux/src/components/cell/index.vue'
import Toast from 'vux/src/components/toast/index.vue'
import Confirm from 'vux/src/components/confirm/index.vue'
import TransferDom from 'vux/src/directives/transfer-dom/index.js'


import url from '../../config/url.js'
import XHeader from 'vux/src/components/x-header/index.vue'




export default {
directives: {
TransferDom
},
components: {
XHeader, // 头部
XButton,
Box,
GroupTitle,
Flexbox,
FlexboxItem,
Divider,
Cell,
Toast,
Confirm
},
data () {
return {
data: '',
show: false,
token:''
}
},

created() {

let vm = this
vm.token = sessionStorage.getItem('token')
this.$ajax({
method:'get',
url:'app/charging',
headers:{'token':vm.token}
})
.then(res=>{

if(res.data.code==400){
console.log(res.data)
vm.Scanning()
}else if(res.data.code==200){
console.log(res.data)
vm.jump('/home/user/order')
}
})
},
methods: {
jump (url) {
this.$router.push(url)
},
Scanning () {
if(sessionStorage.getItem('token')==null){
this.jump('/login')
}else{

let vm = this













vm.chargeSet("802")

}
},
chargeSet (text) {  // 获取二维码点桩中的id，去设置充电类型
this.$router.push({
path: '/chargeSetting',
query:{
chargeId:text
}
})
}
}
}




