
import $ from "jquery";
import XHeader from 'vux/src/components/x-header/index.vue'
import Flexbox from 'vux/src/components/flexbox/flexbox.vue'
import Toast from 'vux/src/components/toast/index.vue'
import Confirm from 'vux/src/components/confirm/index.vue'
import FlexboxItem from 'vux/src/components/flexbox/flexbox-item.vue'
import XInput from 'vux/src/components/x-input/index.vue'
import XButton from 'vux/src/components/x-button/index.vue'
import CheckIcon from 'vux/src/components/check-icon/index.vue'
import Alert from 'vux/src/components/alert/index.vue'
import TransferDom from 'vux/src/directives/transfer-dom/index.js'

export default {
directives: {
TransferDom
},
components: {
XHeader,
Flexbox,
FlexboxItem,
XInput,
XButton,
CheckIcon,
Alert,
Confirm,
Toast
},
data() {
return {
name: "",

one: true,
two: false,
type: 0,

invoice_id: [],
invoice_header: "",
invoice_number: "",
invoice_other: "",
invoice_money: "",
invoice_remarks: "",
invoice_email: "",

submitShow: false,
issubmit: true,
title: "操作提示",
show: false,
toastMsg: "",
showPositionValue: false
};
},
watch: {

one(val) {
let vm = this;
vm.type = 0;
if (val) vm.two = false;
},
two(val) {
let vm = this;
vm.type = 1;
if (val) vm.one = false;
}
},
created() {
let vm = this;
vm.name = sessionStorage.getItem("name");
if (sessionStorage.getItem("token") == null) {
vm.jump("/login");
}

this.invoice_id = JSON.parse(this.$route.query.id);

this.invoice_money = parseFloat(this.$route.query.money).toFixed(2) ;
},
methods: {
jump(url) {
this.$router.push(url);
},

ver_header() {
if (this.invoice_header == "") {
$("input[name='invoice_header']")
.attr("placeholder", "*请填写发票抬头")
.addClass("Verification_style");
}
},
ver_number() {
if (this.invoice_number == "") {
$("input[name='invoice_number']")
.attr("placeholder", "*请填写税号")
.addClass("Verification_style");
}
},





ver_change() {
var pattern = /^([A-Za-z0-9_\-\.])+\@([A-Za-z0-9_\-\.])+\.([A-Za-z]{2,4})$/;
if (this.invoice_email == "") {
$("input[name='invoice_email']")
.attr("placeholder", "*请填写邮箱地址")
.addClass("Verification_style");
} else if (!pattern.test(this.invoice_email)) {
this.invoice_email = "";
$("input[name='invoice_email']")
.attr("placeholder", "*邮箱格式错误")
.addClass("Verification_style");
}
},
onConfirm() {
let vm = this;
vm.$ajax({
method: "post",
url: "/app/orderdata",
headers: { token: sessionStorage.getItem("token") },
data: {
IDS: vm.invoice_id,
GFMC: vm.invoice_header,
GFSH: vm.invoice_number,
BZ: vm.invoice_remarks,
EMAIL: vm.invoice_email
}
}).then(res => {
if (res.data.code == 200) {
console.log("结果", res.data);
vm.jump("/home/invoice/invoiceSuccess");
} else {
vm.showPositionValue = true;
vm.toastMsg = "提交失败";
setTimeout(()=>{
vm.jump("/home/invoice/invoiceInformation");
}, 1200);
}
});
this.issubmit = false;
},
onCancel() {
this.issubmit = true;
this.show = false;
},

submit_ver() {

let vm = this;
if (vm.invoice_header == "" || vm.invoice_email == "") {
vm.ver_header();
vm.ver_change();
return;
}
if (vm.one) {
if (vm.invoice_number == "") {
vm.ver_number();
return;
}
}
if (this.issubmit) {
this.show = true;
} else {
}














}
}
};



