

import Vue from 'vue'
import FastClick from 'fastclick'
import store from './vuex/store.js'
import router from './router'
import App from './App'
import BaiduMap from 'vue-baidu-map'
import VueAMap from 'vue-amap';
import axios from 'axios'
import url from './config/url.js'
import "./assets/less/theme.less"

import VueScroller from 'vue-scroller'
import Fastclick from 'Fastclick'
import ConfirmPlugin from 'vux/src/plugins/confirm/index.js'
import LoadingPlugin from 'vux/src/plugins/loading/index.js'

import * as util from './config/util';

Vue.use(ConfirmPlugin)
Vue.use(LoadingPlugin)

Vue.prototype.util=util;
Vue.prototype.nomalURL=url;

Fastclick.attach(document.body);

Vue.prototype.jump = function () {
if (arguments.length == 1) {
this.$router.push(arguments[0])
} else {
this.$router.push({
path: arguments[0],
query: arguments[1]
})
}
if (arguments.length > 2) {
console.error('参数超过封装好的jump方法')
}
}


let ajax = axios.create({
baseURL: url.LOCALSRC,
timeout: 20000,

})

Vue.prototype.$ajax = ajax
Vue.prototype.token = url.TOKEN

Vue.prototype.slb = function (msg) {  // 全局console.log调试
console.log(msg)
}
Vue.use(VueScroller)
Vue.use(BaiduMap, {

ak: 'h1F6CgQ4MVXXL2smfer6DL0RrKUyyeO0'


})

Vue.prototype.minHeight = document.documentElement.clientHeight;
Vue.prototype.minWidth = document.documentElement.clientWidth;












FastClick.attach(document.body)

/* eslint-disable no-new */
new Vue({
router: router,
store: store,
render: h => h(App),
}).$mount('#app')






