
import Group from 'vux/src/components/group/index.vue'
import Cell from 'vux/src/components/cell/index.vue'
import XButton from 'vux/src/components/x-button/index.vue'
import Box from 'vux/src/components/box/index.vue'
import Toast from 'vux/src/components/toast/index.vue'
import Divider from 'vux/src/components/divider/index.vue'
import Flexbox from 'vux/src/components/flexbox/flexbox.vue'
import FlexboxItem from 'vux/src/components/flexbox/flexbox-item.vue'

import XHeader from 'vux/src/components/x-header/index.vue'
import XInput from 'vux/src/components/x-input/index.vue'

import URL from "../../config/url.js";
import { format } from "../../config/format.js";
import store from "../../vuex/store.js";
import axios from "axios";
import md5 from "../../assets/js/md5.js";

export default {
components: {
Group,
Cell,
XInput,
XButton,
Box,
Toast,
XHeader,
XInput,
Divider,
Flexbox,
FlexboxItem
},
data() {
return {
alipayShow: "none",
qqShow: "none",
wechatShow: "none",
height: "0px",
smsCode: "",
inputSmsCode: "",
show: false,
message: "",
openid: "",
inputphone: "",
disable001: false,
btn: "发送验证码",
phone: "",
passWord: "",
showBtn: true,
toastShow: {
showCancel: false,
cancel_msg: "",
showSuccess: false,
success_msg: "成功登录"
}
};
},
created() {
this.height = this.util.heightPoint(75);
let vm = this;
YCQQ.checkClientInstalled(function(){
vm.qqShow="";
console.log('client is installed');
},function(){

console.log('client is not installed');
});
Wechat.isInstalled(function (installed) {

vm.wechatShow="";
}, function (reason) {
console.log("Failed: " + reason);
});


},
methods: {
check(type) {
let vm = this;




switch (type) {
case 1:

vm.wechatLogin();
break;
case 2:

vm.qqLogin();
break;
case 3:
vm.devConfirm();

break;
}
},

devConfirm() {
let vm = this;
vm.$vux.confirm.show({
title: "提示",
content: "开发中，敬请期待",
confirmText: "确认",
showCancelButton: false,
onConfirm() {}
});
},
oauth(type, code) {
let vm = this;
vm.$ajax({
method: "post",
url: "/app/oAuth",
data: {
type: type,
code: code
}
})
.then(res => {
var data = res.data.data;
if (data.code == 1) {
sessionStorage.setItem("token", data.message);
localStorage.setItem("token", data.message);
vm.$vux.confirm.show({
title: "提示",
content: "登录成功，将跳转到首页",
confirmText: "确认",
showCancelButton: false,
onConfirm() {
vm.$router.push("home/index");
}
});
} else if (data.code == 2) {
vm.$vux.confirm.show({
title: "提示",
content: "登录失败，请重新登录",
confirmText: "确认",
showCancelButton: false
});
} else if (data.code == 3) {
vm.$vux.confirm.show({
title: "提示",
content: "未绑定手机号，请绑定手机号",
confirmText: "确认",
showCancelButton: true,
onCancel() {},
onConfirm() {
let bind = { type: type, unionid: data.message };
vm.$router.push({ name: "bind", params: bind });
}
});
}
})
.catch(res => {
let message = JSON.stringify(res);
vm.$vux.confirm.show({
title: "提示",
content: "请求异常，请稍微再试:",
confirmText: "确认",
showCancelButton: false
});
});
},
qqLogin() {
let vm = this;
var checkClientIsInstalled = 1; 
YCQQ.ssoLogin(
function(args) {


vm.oauth(2, args.userid);
},
function(failReason) {
console.log(failReason);
},
checkClientIsInstalled
);
},
alipayLogin() {
let vm = this;
var scope = "snsapi_userinfo",
state = "_" + +new Date();
var scope = "snsapi_userinfo";
Wechat.auth(
scope,
function(response) {

vm.oauth(1, response.code);
},
function(reason) {

}
);
},

wechatLogin() {
let vm = this;
var scope = "snsapi_userinfo",
state = "_" + +new Date();
var scope = "snsapi_userinfo";
Wechat.auth(
scope,
function(response) {

vm.oauth(1, response.code);
},
function(reason) {

}
);
},

Jump(url) {

this.$router.push(url);
},
NewLogin() {

let vm = this;
vm.$ajax({
method: "post",
url: "/app/auth",
data: {
phone: vm.phone,

password: vm.passWord
}
})
.then(res => {
console.log(res);
if (res.data.code == 200) {
sessionStorage.setItem("token", res.data.data);
sessionStorage.setItem("name", vm.phone);
localStorage.setItem("name", vm.phone);
localStorage.setItem("token", res.data.data);
vm.$router.push("home/index");
} else if (res.data.code == 400) {
vm.toastShow.showCancel = true;
vm.toastShow.cancel_msg = res.data.message;
} else {
vm.toastShow.showCancel = true;
vm.toastShow.cancel_msg = "内部错误";
}
})
.catch(err => {
console.log(err);
});
}
}
};



