
import XHeader from 'vux/src/components/x-header/index.vue'
import Flexbox from 'vux/src/components/flexbox/flexbox.vue'
import FlexboxItem from 'vux/src/components/flexbox/flexbox-item.vue'
import XButton from 'vux/src/components/x-button/index.vue'

export default {
components: {
XHeader,
Flexbox,
FlexboxItem,
XButton,
},
data () {
return {
name:'',
}
},
created () {
let vm = this
vm.name = sessionStorage.getItem('name')
if (sessionStorage.getItem('token') == null){
vm.jump('/login')
}
},
methods:{
jump (url) {
this.$router.push(url)
},

success_con(){
this.$router.push({path:'/home/invoice/invoiceDraw'})
},

success_his(){
this.$router.push({path:'/home/invoice/invoiceList'})
}
}
}



