
import XButton from 'vux/src/components/x-button/index.vue'
import CheckIcon from 'vux/src/components/check-icon/index.vue'

let backpage
export default {
components: {
XButton,
CheckIcon
},
data() {
return {
height: "0px",
footHeight: "0px",
isagree:false,
backpage:''
};
},
beforeRouteEnter(to, from, next) {
backpage=from.path;
next();
},
methods: {







angre() {
if(!this.isagree){
this.$vux.confirm.show({
content: "您需同意《蓉E充充电注册服务协议》方可使用本软件",
confirmText: "确认",
showCancelButton: false
});
}else{
localStorage.setItem("potocol", true);
if(this.backpage=='/register'){
this.$router.push("/register");
}else{
this.$router.push("/home/index");
}

}

}
},
created() {
this.backpage=backpage;
var potocol = localStorage.getItem("potocol");
if(potocol&&this.backpage=='/register'){
this.isagree=true;
}
if (potocol&&this.backpage!=='/register') {
this.$router.push("/home/index");
this.isagree=true;
}
this.height = this.minHeight + "px";
this.footHeight = (this.minHeight / 100) * 88 + "px";
}
};



