
import XButton from 'vux/src/components/x-button/index.vue'
import Box from 'vux/src/components/box/index.vue'
import Toast from 'vux/src/components/toast/index.vue'
import XTextarea from 'vux/src/components/x-textarea/index.vue'
import Group from 'vux/src/components/group/index.vue'

import URL from '../../../config/url.js'
import {format} from '../../../config/format.js'
import store from '../../../vuex/store.js'
import axios from 'axios'

export default {
components: {
XButton,
Box,
Toast,
XTextarea,
Group
},
methods: {
sendFeedback(){

console.log(this.feedback)
const _this = this;

var qs = require('qs');

axios.post(URL.feedback, qs.stringify({phoneNumber: _this.phone, content:_this.feedback}),
{headers: {'Content-Type': 'application/x-www-form-urlencoded'},}).then(function(response) {

if(response.data.errcode == "SUCCESS"){
_this.show = true
}

}).catch(function(error) {
console.log(error);
})
},
backToMine(){
this.$router.push('/home/mine')
}
},
data () {
return {
balance:'XXX',
show:false,
message:'提交成功',
feedback:'',
phone:store.getters.getPhone,
};
},
}



