isAndroid() {
var u = navigator.userAgent;
var Android = u.indexOf("Android") > -1 || u.indexOf("Linux") > -1; 
var isIOS = !!u.match(/\(i[^;]+;( U;)? CPU.+Mac OS X/); 
console.log(u);
if (Android) {
console.log("安卓机！");
this.isAndroid = true;
return true;
}
if (isIOS) {
console.log("苹果机！");
this.isAndroid = false;
return false;
}
console.log("不是android也不是ios");
return false;
