
import Tabbar from 'vux/src/components/tabbar/tabbar.vue'
import TabbarItem from 'vux/src/components/tabbar/tabbar-item.vue'


export default {
components: {
Tabbar,
TabbarItem
},
data(){
return{
index:0
}

},

methods:{

Scanning() {
if (sessionStorage.getItem("token") == null) {
this.$router.push("/login");
} else {
let vm = this;
vm.token = sessionStorage.getItem("token");
this.$ajax({
method: "get",
url: "app/charging",
headers: { token: vm.token }
}).then(res => {
if (res.data.code == 400) {















cloudSky.zBar.scan(
{
text_title: "", // Android only
text_instructions: "请把相机中的红线对准二维码", // Android only
camera: "back", // defaults to "back"
flash: "auto", // defaults to "auto". See Quirks
drawSight: true 
},
function(s) {
if (s !== "") {
vm.chargeSet(s);
} else {
vm.$router.push("/home/bmapV");
}
},
function(s) {
console.log("扫码失败：", s);
}
);
} else if (res.data.code == 200) {

console.log(res.data);
vm.$router.push("/home/user/order");
}
});

}
},
chargeSet(text) {

this.$router.push({
path: "/chargeSetting",
query: {
chargeId: text
}
});
},
}
}



