
import store from "../../../config/store.js";
let collectionIdArr = []; 

import XHeader from 'vux/src/components/x-header/index.vue'
import Group from 'vux/src/components/group/index.vue'
import Cell from 'vux/src/components/cell/index.vue'
import Scroller from 'vux/src/components/scroller/index.vue'
import XButton from 'vux/src/components/x-button/index.vue'
import Flexbox from 'vux/src/components/flexbox/flexbox.vue'
import FlexboxItem from 'vux/src/components/flexbox/flexbox-item.vue'
import Popup from 'vux/src/components/popup/index.vue'
import Divider from 'vux/src/components/divider/index.vue'

export default {
components: {
XHeader,
Group,
Cell,
Scroller,
XButton,
Flexbox,
FlexboxItem,
Popup,
Divider,
},
data() {
return {
data: [],
selectMap: false,
center: { lng: 0, lat: 0 },
autoLocationPoint: { lng: 0, lat: 0 },
distance: "",
distance2:"",
name: "",
height: "auto",
overuser:[]
};
},
created() {
let vm=this;
collectionIdArr = JSON.parse(localStorage.getItem("collectionIdArr"));
vm.autoLocationPoint =  eval('(' + sessionStorage.getItem("autoLocationPoint")+ ')');  

function uniq(arr) {
var temp = []; 
for (var i = 0; i < arr.length; i++) {
if (temp.indexOf(arr[i]) == -1) {
temp.push(arr[i]);
}
}
return temp;
}
collectionIdArr = uniq(collectionIdArr);
console.log("去重后的收藏id数组", collectionIdArr);
this.$ajax({
method: "post",
url: "/app/chargingNet/collects",
data: collectionIdArr,
headers: { token: sessionStorage.getItem("token") }
}).then(res => {
if (res.data.code == 200) {
console.log("data------", res.data);
this.data = res.data.data.collects;
this.overuser=res.data.data.overuser;
this.data.map(item =>{
vm.center.lng = item.longitude
vm.center.lat = item.latitude
var distance = vm.GetDistance(vm.center.lat,vm.center.lng,vm.autoLocationPoint.lat,vm.autoLocationPoint.lng)
vm.distance = distance.toFixed(2)
item.distance = vm.distance
})
this.overuser.map(item=>{
vm.center.lng = item.longitude
vm.center.lat = item.latitude
var distance2 = vm.GetDistance(vm.center.lat,vm.center.lng,vm.autoLocationPoint.lat,vm.autoLocationPoint.lng)
vm.distance2 = distance2.toFixed(2)
item.distance = vm.distance2
})
}
});
},
methods: {
jump(url) {
this.$router.push(url);
},

Rad(d){
return d * Math.PI / 180.0;
},

GetDistance(lat1, lng1, lat2, lng2) {
var radLat1 = this.Rad(lat1);
var radLat2 = this.Rad(lat2);
var a = radLat1 - radLat2;
var b = this.Rad(lng1) - this.Rad(lng2);
var s = 2 * Math.asin(Math.sqrt(Math.pow(Math.sin(a/2),2) +
Math.cos(radLat1)*Math.cos(radLat2)*Math.pow(Math.sin(b/2),2)));
s = s * 6378.137; 
s = Math.round(s * 10000) / 10000; 

return s;
},

Navigation(name, deslat, deslon) {
let vm = this;
this.selectMap = false;
setTimeout(function() {
vm.selectMap = true;
}, 1000);
this.name = name;
this.center.lng = deslon;
this.center.lat = deslat;
},

ChargeMsg(chargeId) {
let data = [];
let Time = new Date();
this.$ajax({
method: "get",
url: "/api/rate/" + chargeId,
headers: { token: sessionStorage.getItem("token") }
}).then(res => {
if (res.data.code == 200) {
let tableDataTime = [
"00:30",
"01:00",
"01:30",
"02:00",
"02:30",
"03:00",
"03:30",
"04:00",
"04:30",
"05:00",
"05:30",
"06:00",
"06:30",
"07:00",
"07:30",
"08:00",
"08:30",
"09:00",
"09:30",
"10:00",
"10:30",
"11:00",
"11:30",
"12:00",
"12:30",
"13:00",
"13:30",
"14:00",
"14:30",
"15:00",
"15:30",
"16:00",
"16:30",
"17:00",
"17:30",
"18:00",
"18:30",
"19:00",
"19:30",
"20:00",
"20:30",
"21:00",
"21:30",
"22:00",
"22:30",
"23:00",
"23:30",
"24:00"
];
let ePriceArr = [];
let fPriceArr = [];
let arr = eval("(" + res.data.data.rate + ")"); 
for (var j = 0; j < arr.length; j++) {
let ePrice = arr[j].ePrice / 100;
let sPrice = arr[j].sPrice / 100;
ePriceArr.push({
time: tableDataTime[j],
value: ePrice,
type: "电费"
});
fPriceArr.push({
time: tableDataTime[j],
value: sPrice,
type: "服务费"
});
}
data = ePriceArr.concat(fPriceArr);
let dataStr = JSON.stringify(data); 
this.$router.push({
path: "/chargeDetails",
query: {
id: chargeId,
price: dataStr,
Exhibition: "/home/SiteListV"
}
});
}
});
}
}
};



