
import XHeader from 'vux/src/components/x-header/index.vue'
import Divider from 'vux/src/components/divider/index.vue'
import Scroller from 'vux/src/components/scroller/index.vue'
import Flexbox from 'vux/src/components/flexbox/flexbox.vue'
import FlexboxItem from 'vux/src/components/flexbox/flexbox-item.vue'
import Checker from 'vux/src/components/checker/checker.vue'
import CheckerItem from 'vux/src/components/checker/checker-item.vue'

import $ from 'jquery';
export default {
components: {
XHeader,
Divider,
Scroller,
Flexbox,
FlexboxItem,
Checker,
CheckerItem
},
data () {
return {
name:'',
dataEnvelopes: [],
value:'',
height:'',
useNum:0,
c_money:0,
}
},
mounted () {
let Height = window.innerHeight - 80
this.height =Height+'px'
},
methods:{
getRedEnvelopes(){
let vm = this
vm.$ajax({
method:'get',
url:'/app/activity/unusedRedPacket',
headers:{'token':sessionStorage.getItem('token')},
}).then(res => {
if(res.data.code == 200){

let oneArr = []
let twoArr = []
res.data.data.map(item => {
if(item.aid == 2){
twoArr.push(item)
}else{
oneArr.push(item)
}
})
let sliceArr = twoArr.slice(0,vm.useNum)
vm.dataEnvelopes = oneArr.concat(sliceArr)
console.log(vm.dataEnvelopes)
}
})
},
chooseSuccess(id,r_money){

let useNum = this.$route.query.redIndex - 1
sessionStorage.setItem('rid',id)
this.$router.push({path:'/home/user/appCash',query:{r_money:r_money,c_money:this.c_money,useNum:useNum}})

}
},
created () {

this.useNum = this.$route.query.redIndex
this.c_money = this.$route.query.money
this.getRedEnvelopes()
}
}



