
import XButton from 'vux/src/components/x-button/index.vue'
import CheckIcon from 'vux/src/components/check-icon/index.vue'


export default {
components: {
XButton,
CheckIcon
},
data() {
return {
height: "0px",
footHeight: "0px",
isagree:false
};
},
methods: {

angre() {
if(!this.isagree){
this.$vux.confirm.show({
content: "您需同意《蓉E充发票规则》方可使用本软件",
confirmText: "确认",
showCancelButton: false
});
}else{
localStorage.setItem("isread", true);
this.$router.push("/home/invoice/invoiceInformation");
}

}
},
created() {
this.height = this.minHeight + "px";
this.footHeight = (this.minHeight / 100) * 88 + "px";
}
};



