
import Cell from 'vux/src/components/cell/index.vue'
import Flexbox from 'vux/src/components/flexbox/flexbox.vue'
import FlexboxItem from 'vux/src/components/flexbox/flexbox-item.vue'
import Scroller from 'vux/src/components/scroller/index.vue'

export default {
components:{
Cell,
Flexbox,
FlexboxItem,
Scroller
},
data () {
return {
chargeStatic:[],
height:''
}
},
mounted () {
let Height = window.innerHeight - 90
this.height =Height+'px'
},
created () {
let vm = this
vm.$ajax({
method:"get",
url:'/api/pile/site/'+this.$route.query.id+'?page=1&size=100',
})
.then(res=>{
console.log('狀態：')
console.log(res)
if(res.data.code==200){
vm.chargeStatic = res.data.data.list
console.log(res.data.data.list)
}
console.log(666)
console.log(vm.chargeStatic)
console.log(666)
})
}
}



