

function uniq(arr) {
var temp = []; 
for (var i = 0; i < arr.length; i++) {
if (temp.indexOf(arr[i]) == -1) {
temp.push(arr[i]);
}
}
return temp;
}
import XHeader from 'vux/src/components/x-header/index.vue'
import Tab from 'vux/src/components/tab/tab.vue'
import TabItem from 'vux/src/components/tab/tab-item.vue'
import Rater from 'vux/src/components/rater/index.vue'
import Toast from 'vux/src/components/toast/index.vue'
import Swiper from 'vux/src/components/swiper/swiper.vue'
import SwiperItem from 'vux/src/components/swiper/swiper-item.vue'
import Flexbox from 'vux/src/components/flexbox/flexbox.vue'
import FlexboxItem from 'vux/src/components/flexbox/flexbox-item.vue'

import TabDetails from "./tabDetails.vue";
import TabStatic from "./tabStatic.vue";
import TabComment from "./tabComment.vue";
import store from "../../vuex/store.js";
let collectionIdArr = [];
export default {
components: {
XHeader,
Tab,
TabItem,
Rater,
Toast,
Swiper,
SwiperItem,
TabDetails,
TabStatic,
TabComment,
Flexbox,
FlexboxItem
},
data() {
return {
data: [],
collection: 0,
toastMsg: "",
showPositionValue: false,
del: true,
com: false,
transitionName: "slide-left",
Exhibition: "",
list2: ["详情", "状态"],
demo2: "详情",
index: 0,
cid:0,
};
},
mounted() {
console.log(localStorage.getItem("CollectionArray"));

this.StarIndex();
this.cid=parseInt(this.$route.query.id);

},
watch: {
index(val) {
console.log(val);
let vm = this;
if (val == 0) {
vm.demo2 = "详情";
} else if (val == 1) {
vm.demo2 = "状态";
}



},
collection(val) {
let vm = this;
if (val == 1) {
if (localStorage.getItem("collectionIdArr") == null) {
vm.showPositionValue = true;
vm.toastMsg = "收藏成功";


collectionIdArr.push(this.cid);
localStorage.setItem(
"collectionIdArr",
JSON.stringify(collectionIdArr)
);
} else {


collectionIdArr.push(this.cid);
collectionIdArr = uniq(collectionIdArr);

let arr = JSON.parse(localStorage.getItem("collectionIdArr"));
if(arr.indexOf(this.cid)==-1){
vm.showPositionValue = true;
vm.toastMsg = "收藏成功";
}

localStorage.setItem(
"collectionIdArr",
JSON.stringify(collectionIdArr)
);



}
} else {
vm.toastMsg = "取消收藏";
vm.showPositionValue = true;
collectionIdArr = uniq(
JSON.parse(localStorage.getItem("collectionIdArr"))
);
for (let i = 0; i < collectionIdArr.length; i++) {
if (collectionIdArr[i] == this.cid) {
collectionIdArr.splice(i, 1);

}
}

localStorage.setItem(
"collectionIdArr",
JSON.stringify(collectionIdArr)
);
}
}
},
beforeRouteUpdate(to, from, next) {
let isBack = this.$router.isBack;
if (isBack) {
this.transitionName = "slide-right";
} else {
this.transitionName = "slide-left";
}
this.$router.isBack = false;
next();
},
created() {
this.getComment();
this.GetChargeMsg();
if (this.$route.query.comment == 1) {

this.com = true;
this.del = false;
} else {
this.com = false;
this.del = true;
}
this.Exhibition = this.$route.query.Exhibition;
},
methods: {



getComment() {








},
GetChargeMsg() {

let vm = this;
vm.$ajax("/app/chargingNet/" + vm.$route.query.id).then(res => {


vm.data.push({
name: res.data.data.name,
addr: res.data.data.addr,
id: res.data.data.id
});
console.log("020202156", vm.data);
});
},
StarIndex() {
let arr = [];
let vm = this;


arr = JSON.parse(localStorage.getItem("collectionIdArr"));

if (arr != null) {
let array = uniq(arr); 

for (var i = 0; i < array.length; i++) {
console.log("进入循环");
console.log(i);
if (array[i] == vm.$route.query.id) {
console.log("存在");
vm.collection = 1;
}
}
}
}
}
};



