
import XHeader from 'vux/src/components/x-header/index.vue'
import Group from 'vux/src/components/group/index.vue'
import Cell from 'vux/src/components/cell/index.vue'
import Calendar from 'vux/src/components/calendar/index.vue'
import Scroller from 'vux/src/components/scroller/index.vue'
import Flexbox from 'vux/src/components/flexbox/flexbox.vue'
import FlexboxItem from 'vux/src/components/flexbox/flexbox-item.vue'
import XButton from 'vux/src/components/x-button/index.vue'
import Confirm from 'vux/src/components/confirm/index.vue'
import XDialog from 'vux/src/components/x-dialog/index.vue'
import XSwitch from 'vux/src/components/x-switch/index.vue'
import TransferDom from 'vux/src/directives/transfer-dom/index.js'


export default {
directives: {
TransferDom
},
components: {
XHeader,
Group,
Cell,
Scroller,
Flexbox,
FlexboxItem,
XButton,
Confirm,
XSwitch,
XDialog ,
Calendar,
},
data () {
return {
name:'',
data: {},
djhm:'',
testdjhm:"",
show:false,
showvoice:false,
voicetitle:'发票信息',
showToast: false,
invoiceId:"",
pdfFile:''
}
},
created () {
let vm = this

vm.djhm = vm.$route.query.invoiceId
if(!vm.djhm){
vm.djhm=sessionStorage.getItem("invoicedjhm")
}else{
sessionStorage.setItem("invoicedjhm",vm.djhm)
}

vm.name = sessionStorage.getItem('name')
if (sessionStorage.getItem('token') == null){
vm.jump('/login')
} else {
this.Get_invoice_detail()
}
},
methods:{
jump (url) {
this.$router.push({path:url,query:{'testdjhm':this.testdjhm}})
},
showvoice1(){
this.showvoice=true;
},
Get_invoice_detail () {
let vm = this



vm.$ajax({
method:'get',
url:'/app/invoice/'+vm.djhm,
headers:{'token':sessionStorage.getItem('token')},
})
.then(res=>{
if(res.data.code == 200){
vm.data = res.data.data
vm.invoiceId=res.data.data.id
vm.testdjhm=res.data.data.djhm
vm.pdfFile=this.nomalURL.nomalURL+"/data/pdf/"+res.data.data.djhm+".pdf"
console.log("详情")
console.log(vm.data)
console.log("详情")
sessionStorage.setItem("voiceid",this.testdjhm)
}
})
},

reset_invoice(){
this.$router.push({path:'/home/invoice/invoiceReset',query:{"djhm":this.testdjhm}})
},

customer_hotline(){
this.show = true
},
onConfirm () {

console.log('发起拨打客服热线的请求')
}
}
}



